@media only screen and (max-width: 768px) {
  .menu back{
    display: block !important;
    z-index: 1;
  }
  .tripPage{
    display:flex;
    flex-direction: column;
    font-size: smaller;
  }
  
    .headline{
      color:#ffffff;
      text-align: center;
      font-weight: 900;
        font-size: 40px;
        font-family: "Poppins", Arial, sans-serif;
    }
    
    .headlineBox{
      width:90%;
      display:flex;
      margin-top: 8%;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
    .headlineBox .fas{
      color:white;
      font-size: 30px;
      cursor: pointer;
    }
  
  .back button{
