    margin-left: 10px;
    color: #404040;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 10px;
    background-color: transparent;
      border: solid;
      border-color: #404040;
      padding: 5px 16px;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      border-radius: 10px;
      cursor: pointer;
  }
  .back_arrow{
    margin-left:2%;
    width:10px;
    cursor: pointer;
    color:#404040;
  }
  .back_arrow:hover, .back_arrow:focus{
    text-decoration-line: underline;
    text-decoration-style: solid;
    color:#404040;
    cursor: pointer;
  }
  .back_button:hover{
    text-decoration-line: underline;
    text-decoration-style: solid;
    cursor: pointer;
  }
  .back_button:focus{
    outline:0;
  }
